.promo-sublist {
  @media screen and (min-width: $desktop-width) {
    display: flex;

    @include list-reset;
  }
}

.promo-sublist__link {
  padding: 19px 22.5px;
  text-decoration: none;
  color: $titleTextColor;

  @include font-style(15px, 24px, 700);

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media screen and (min-width: $desktop-width) {
    &:first-child {
      padding-left: 0;
    }
  }
}
