@charset "UTF-8";
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/OpenSans-Regular.woff2"), url("../../fonts/OpenSans-Regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/OpenSans-Bold.woff2"), url("../../fonts/OpenSans-Bold.woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.nowrap {
  white-space: nowrap;
}

.hide {
  display: none;
}

*:hover {
  transition: 50ms;
}

.page {
  height: 100%;
  background: rgb(255, 255, 255);
  color: rgb(68, 68, 68);
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
}

.page__body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.button {
  display: block;
  padding: 14px;
  margin: 0 auto;
  background-color: rgb(255, 255, 255);
  text-transform: uppercase;
  border: 2px solid rgb(68, 68, 68);
  color: rgb(68, 68, 68);
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  font-size: 18px;
  line-height: 20px;
  font-weight: 700;
}
.button:hover {
  background-color: rgb(68, 68, 68);
  color: rgb(255, 255, 255);
}
.button:active .button__text {
  opacity: 0.3;
}
.button--modal {
  background-color: rgb(99, 209, 187);
  color: rgb(255, 255, 255);
  padding: 18px;
  border: none;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
}

.about {
  padding: 0 30px;
}
@media screen and (min-width: 768px) {
  .about {
    padding: 0 50px;
  }
}
@media screen and (min-width: 1150px) {
  .about {
    padding-left: 224px;
    padding-right: 224px;
  }
}

.about__title {
  margin: 0;
  margin-bottom: 28px;
  color: rgb(0, 0, 0);
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .about__title {
    margin-bottom: 42px;
    font-size: 43px;
    line-height: 50px;
    font-weight: 700;
  }
}
@media screen and (min-width: 1150px) {
  .about__title {
    font-size: 53px;
    line-height: 57px;
    font-weight: 700;
  }
}

.about__item {
  color: rgb(0, 0, 0);
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
}
.about__item::before {
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  content: "";
}
.about__item--eco::before {
  margin-bottom: 16px;
  width: 33px;
  height: 38px;
  background-image: url("../img/icon-eco.svg");
}
.about__item--price::before {
  margin-bottom: 26px;
  width: 47px;
  height: 27px;
  background-image: url("../img/icon-price.svg");
}
.about__item--likes::before {
  margin-bottom: 21px;
  width: 34px;
  height: 27px;
  background-image: url("../img/icon-like.svg");
}
.about__item--circle::before {
  margin-bottom: 17px;
  width: 35px;
  height: 34px;
  background-image: url("../img/icon-circle.svg");
}
.about__item--support::before {
  margin-bottom: 26px;
  width: 55px;
  height: 23px;
  background-image: url("../img/icon-racket.svg");
}
.about__item--present::before {
  margin-bottom: 20px;
  width: 33px;
  height: 32px;
  background-image: url("../img/icon-present.svg");
}
@media screen and (min-width: 768px) {
  .about__item {
    font-size: 20px;
    line-height: 22px;
    font-weight: 700;
  }
}

.about__list {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media screen and (min-width: 550px) {
  .about__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    column-gap: 63px;
    row-gap: 57px;
    grid-auto-flow: column;
  }
}

.about-production {
  display: flex;
  flex-direction: column;
  padding: 22px 0 33px;
  margin: 0 30px 58px;
  border-bottom: 1px solid rgb(236, 236, 236);
  border-top: 1px solid rgb(236, 236, 236);
}
@media screen and (min-width: 768px) {
  .about-production {
    margin: 0 50px;
    border-top: none;
  }
}
@media screen and (min-width: 1150px) {
  .about-production {
    display: grid;
    grid-template-columns: 682px 288px;
    grid-template-rows: min-content 1fr;
    column-gap: 80px;
    padding: 0;
    border-top: 1px solid rgb(236, 236, 236);
  }
}

.about-production__title {
  display: block;
  width: min-content;
  color: rgb(0, 0, 0);
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
}
@media screen and (min-width: 550px) {
  .about-production__title {
    width: 100%;
  }
}
@media screen and (min-width: 1150px) {
  .about-production__title {
    width: 50%;
    margin-bottom: 27px;
  }
}

.about-production__title-wrapper {
  display: flex;
  order: -1;
  align-items: center;
  margin-bottom: 27px;
  margin-left: 34px;
}
.about-production__title-wrapper::before {
  display: block;
  margin-right: 25px;
  width: 32px;
  height: 22px;
  background-image: url("../img/video.svg");
  background-position: center;
  background-repeat: no-repeat;
  content: "";
}
@media screen and (min-width: 768px) {
  .about-production__title-wrapper {
    order: 0;
  }
}
@media screen and (min-width: 1150px) {
  .about-production__title-wrapper {
    display: block;
    margin: 0;
    padding-top: 47px;
  }
  .about-production__title-wrapper::before {
    margin-bottom: 27px;
  }
}

.about-production__description {
  margin: 0;
  margin-bottom: 41px;
  font-size: 13px;
  line-height: 24px;
  font-weight: 400;
}
@media screen and (min-width: 768px) {
  .about-production__description {
    font-size: 15px;
    line-height: 30px;
    font-weight: 400;
  }
}
@media screen and (min-width: 1150px) {
  .about-production__description {
    margin: 0;
    margin-bottom: 52px;
  }
}

.about-production__video {
  position: relative;
  margin-bottom: 35px;
  text-align: center;
}
@media screen and (min-width: 1150px) {
  .about-production__video {
    grid-row: 1/span 2;
    margin-bottom: 0;
  }
}

.about-production__play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 102px;
  height: 78px;
  background-image: url("../img/play.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: "";
}
@media screen and (min-width: 1150px) {
  .about-production__play-button:hover {
    width: 121px;
    height: 92px;
  }
  .about-production__play-button:active {
    width: 81px;
    height: 62px;
    opacity: 0.3;
  }
}

.about-production__wrapper {
  margin-left: 34px;
}
@media screen and (min-width: 1150px) {
  .about-production__wrapper {
    margin: 0;
  }
}

.about-production__picture {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
  max-height: 455px;
}

.about-production__button {
  width: 192px;
}
@media screen and (min-width: 768px) {
  .about-production__button {
    width: 584px;
  }
}
@media screen and (min-width: 1150px) {
  .about-production__button {
    width: 230px;
    margin: 0;
  }
}

.arrow {
  background-color: rgb(255, 255, 255);
  border: none;
  width: 50px;
  height: 26px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.arrow:hover {
  opacity: 0.6;
}
.arrow:active {
  opacity: 0.3;
}

.arrow--left {
  background-image: url("../img/arrow-left.svg");
  margin-left: 38px;
}
@media screen and (min-width: 768px) {
  .arrow--left {
    margin-left: 19px;
  }
}

.arrow--right {
  background-image: url("../img/arrow-right.svg");
  margin-right: 42px;
}
@media screen and (min-width: 768px) {
  .arrow--right {
    margin-right: 19px;
  }
}

.arrow--disabled {
  opacity: 0.3;
  cursor: auto;
}
.arrow--disabled:hover {
  opacity: 0.3;
}

.catalog {
  padding: 0 30px 30px;
}
@media screen and (min-width: 768px) {
  .catalog {
    padding: 0 50px;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media screen and (min-width: 920px) {
  .catalog__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(316px, 1fr));
    column-gap: 51px;
    row-gap: 50px;
  }
}

.catalog__item {
  border-bottom: 1px solid rgb(236, 236, 236);
  margin: 0 auto;
  margin-bottom: 30px;
}
@media screen and (min-width: 768px) {
  .catalog__item {
    border-top: 1px solid rgb(236, 236, 236);
  }
}
@media screen and (min-width: 1150px) {
  .catalog__item {
    margin-bottom: 0;
  }
}

.categories {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media screen and (min-width: 550px) {
  .categories {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.categories__item {
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-content: center;
  padding: 10px 30px;
  min-height: 103px;
  background-color: rgb(99, 209, 187);
  box-sizing: border-box;
  font-size: 19px;
  line-height: 24px;
  font-weight: 700;
}
.categories__item--dark {
  background-color: rgb(86, 195, 178);
}
.categories__item--interior::after {
  display: block;
  width: 40px;
  height: 62px;
  background-image: url("../img/icon-interior.svg");
  content: "";
}
.categories__item--toy::after {
  display: block;
  width: 60px;
  height: 60px;
  background-image: url("../img/icon-toy.svg");
  content: "";
}
.categories__item::after {
  justify-self: end;
  align-self: center;
}
@media screen and (min-width: 550px) {
  .categories__item {
    width: 100%;
    min-height: 130px;
    align-content: start;
    padding-top: 30px;
    padding-bottom: 34px;
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
  }
}
@media screen and (min-width: 920px) {
  .categories__item {
    min-height: 134px;
    padding-bottom: 42px;
  }
  .categories__item:hover {
    transform: translateY(-5px);
  }
  .categories__item:active::after {
    opacity: 0.3;
  }
}

.categories__link {
  color: rgb(255, 255, 255);
  text-align: left;
  text-decoration: none;
  cursor: pointer;
}

.checkbox-field {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 23px;
  min-height: 38px;
}
@media screen and (min-width: 1150px) {
  .checkbox-field:hover .checkbox-field__mark {
    border-color: rgb(99, 209, 187);
  }
  .checkbox-field:active .checkbox-field__mark {
    border-color: rgba(68, 68, 68, 0.3);
  }
}

.checkbox-field__label {
  color: rgb(0, 0, 0);
  padding-left: 61px;
  font-size: 17px;
  line-height: 18px;
  font-weight: 700;
}

.checkbox-field__input {
  appearance: none;
}

@media screen and (min-width: 1150px) {
  .checkbox-field__input:focus + .checkbox-field__mark {
    border-color: rgb(99, 209, 187);
  }
}

.checkbox-field__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 38px;
  height: 38px;
  border: 2px solid rgb(68, 68, 68);
  z-index: 0;
  content: "";
}

.checkbox-field__input:checked + .checkbox-field__mark::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  background-image: url("../img/tick.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 24px;
  height: 17px;
  z-index: 3;
  content: "";
}

.contacts {
  position: relative;
}
@media screen and (min-width: 768px) {
  .contacts {
    box-sizing: border-box;
  }
}
@media screen and (min-width: 1150px) {
  .contacts {
    display: grid;
    grid-template-columns: 299px 527px;
    column-gap: 55px;
  }
}
@media screen and (min-width: 768px) {
  .contacts::before {
    position: absolute;
    top: 0;
    right: 50px;
    width: 47px;
    height: 81px;
    background-image: url("../img/flag.svg");
    background-repeat: no-repeat;
    background-position: center;
    content: "";
  }
}
@media screen and (min-width: 1150px) {
  .contacts::before {
    left: 63px;
    top: 62px;
  }
}

.contacts__title {
  margin: 0;
  margin-bottom: 28px;
  padding: 0 30px;
  color: rgb(0, 0, 0);
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .contacts__title {
    padding: 0 50px;
    padding-top: 22px;
    margin-bottom: 49px;
    font-size: 43px;
    line-height: 50px;
    font-weight: 700;
  }
}
@media screen and (min-width: 1150px) {
  .contacts__title {
    font-size: 53px;
    line-height: 57px;
    font-weight: 700;
    padding: 0;
    margin-top: 79px;
    margin-bottom: 35px;
  }
}

.contacts__map {
  grid-row: 1/span 3;
  grid-column: 2/span 1;
  margin-bottom: 42px;
}
@media screen and (min-width: 768px) {
  .contacts__map {
    margin-bottom: 55px;
  }
}
@media screen and (min-width: 1150px) {
  .contacts__map {
    margin-bottom: 0;
  }
}

.contacts__button {
  width: 260px;
}
@media screen and (min-width: 768px) {
  .contacts__button {
    width: 230px;
  }
}

.contacts-list {
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin: 0;
  margin-bottom: 62px;
  padding: 0 30px;
}
@media screen and (min-width: 550px) {
  .contacts-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 49px;
    padding: 0 50px;
  }
}
@media screen and (min-width: 1150px) {
  .contacts-list {
    padding: 0;
    display: flex;
    flex-direction: column;
    margin-bottom: 84px;
    gap: 30px;
  }
}

.contacts-list__row {
  display: flex;
  gap: 30px;
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
}
@media screen and (min-width: 768px) {
  .contacts-list__row {
    font-size: 17px;
    line-height: 30px;
    font-weight: 400;
  }
}

.contacts-list__value {
  margin: 0;
}

.contacts-list__link {
  position: relative;
  color: rgb(68, 68, 68);
  text-decoration: none;
}
.contacts-list__link::after {
  position: absolute;
  bottom: -2px;
  left: 0;
  background-color: rgb(99, 209, 187);
  height: 1px;
  width: 100%;
  content: "";
}
.contacts-list__link:hover {
  color: rgb(99, 209, 187);
}
.contacts-list__link:active::after {
  opacity: 0.1;
}
@media screen and (min-width: 1150px) {
  .contacts-list__link::after {
    bottom: -5px;
  }
}

.copyright {
  text-align: center;
  padding-top: 22px;
  border-top: 1px solid rgb(229, 229, 229);
}
@media screen and (min-width: 768px) {
  .copyright {
    border: none;
    padding-top: 0;
  }
}

.copyright__text {
  color: rgb(0, 0, 0);
  margin-right: 27px;
  font-size: 19px;
  line-height: 18px;
  font-weight: 700;
}

.copyright__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  margin: 0;
  padding: 0;
}
.copyright__link:hover .copyright__logo {
  color: rgb(70, 193, 174);
}
.copyright__link:active .copyright__logo {
  color: rgb(70, 193, 174);
  opacity: 0.3;
}

.copyright__logo {
  color: rgb(99, 209, 187);
}

.header {
  position: relative;
  min-height: 79px;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .header {
    display: flex;
    min-height: 89px;
  }
}
@media screen and (min-width: 1150px) {
  .header {
    justify-content: space-between;
  }
}

.header__logo {
  display: block;
}
@media screen and (min-width: 768px) {
  .header__logo:hover {
    opacity: 0.6;
  }
  .header__logo:active {
    opacity: 0.3;
  }
}
@media screen and (min-width: 1150px) {
  .header__logo {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
  }
}

.header__logo-image {
  display: block;
  padding: 22px 0;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .header__logo-image {
    padding: 22px 20px 20px;
    width: 114px;
    height: 46px;
  }
}

.item {
  display: flex;
  flex-direction: column;
  width: min-content;
}
@media screen and (min-width: 550px) {
  .item {
    flex-direction: row;
    width: 100%;
  }
}
@media screen and (min-width: 920px) {
  .item {
    flex-direction: column;
  }
  .item:hover .item__wrapper {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }
  .item:hover .item__title {
    opacity: 0.6;
  }
  .item:active .item__wrapper {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  }
  .item:active .item__title {
    opacity: 0.3;
  }
}

.item__title {
  margin: 0;
  margin-bottom: 6px;
  color: rgb(0, 0, 0);
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
}
@media screen and (min-width: 550px) {
  .item__title {
    padding-top: 27px;
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
  }
}

.item__specs {
  display: block;
  padding-bottom: 18px;
  border-bottom: 1px solid rgb(236, 236, 236);
  font-size: 13px;
  line-height: 30px;
  font-weight: 400;
}
@media screen and (min-width: 768px) {
  .item__specs {
    border-bottom: none;
    padding-bottom: 0;
    font-size: 15px;
    line-height: 30px;
    font-weight: 400;
  }
}
@media screen and (min-width: 768px) {
  .item__specs {
    margin-bottom: 12.5px;
  }
}

.item__price {
  color: rgb(0, 0, 0);
  font-size: 15px;
  line-height: 24px;
  font-weight: 700;
}
@media screen and (min-width: 768px) {
  .item__price {
    font-size: 17px;
    line-height: 24px;
    font-weight: 700;
  }
}

.item__wrapper {
  padding: 0 28px;
}
@media screen and (min-width: 768px) {
  .item__wrapper {
    display: grid;
    grid-template-rows: min-content auto min-content;
    grid-template-columns: 100%;
    padding: 0 33px;
    width: 100%;
  }
}
@media screen and (min-width: 1150px) {
  .item__wrapper {
    padding: 0 28px;
    height: 100%;
    border: 1px solid rgb(236, 236, 236);
    box-sizing: border-box;
  }
}

.item__image {
  display: block;
  margin-bottom: 28px;
  object-fit: cover;
}
@media screen and (min-width: 768px) {
  .item__image {
    margin: 0;
    width: 100%;
    height: 100%;
    max-height: 334px;
    object-fit: cover;
  }
}
@media screen and (min-width: 1150px) {
  .item__image {
    max-height: 462px;
  }
}

.item__button {
  display: flex;
  justify-content: space-between;
  padding: 23px 0 26px;
  text-decoration: none;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .item__button {
    border-top: 1px solid rgb(236, 236, 236);
  }
}
@media screen and (min-width: 1150px) {
  .item__button:hover .item__icon {
    color: rgb(99, 209, 187);
    opacity: 1;
  }
  .item__button:active .item__icon {
    opacity: 0.3;
  }
}

.item__icon {
  opacity: 0.2;
}

.footer {
  min-height: 160px;
  background-color: rgb(241, 241, 241);
  box-sizing: border-box;
}
@media screen and (min-width: 768px) {
  .footer {
    display: flex;
    align-items: center;
    min-height: 143px;
  }
}

.footer__logo {
  color: rgb(0, 0, 0);
}
.footer__logo:hover .footer__logo-image {
  color: rgb(99, 209, 187);
