  background-color: transparent;
  z-index: 4;
}
.toggle::before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 20px;
  height: 2px;
  background-color: rgb(35, 31, 32);
  box-shadow: 0 6px 0 0 rgb(35, 31, 32), 0 12px 0 0 rgb(35, 31, 32);
  content: "";
}
