.textarea-field {
  padding: 30px 0 17px;

  @media screen and (min-width: $tablet-width) {
    flex-grow: 1;
    padding: 0;
    padding-right: 50px;
  }

  @media screen and (min-width: $desktop-width) {
    padding-right: 0;
  }
}

.textarea-field__input {
  width: 100%;
  padding: 17px;
  box-sizing: border-box;
  font-family: "OpenSans", "Arial", sans-serif;
  border: 2px solid $primaryTextColor;

  @include font-style(17px, 18px, 400);

  @media screen and (min-width: $desktop-width) {
    &:hover, &:focus {
      border-color: $primaryColor;
    }

    &:focus-visible {
      outline: none;
    }
  }
}

.textarea-field__input::placeholder {
  opacity: 0.3;
}
