.nav__list {
  @include list-reset;
}

.nav__link {
  display: block;
  padding: 28px 30px 27px 76px;
  border-bottom: 1px solid $borderColor;
  box-sizing: border-box;
  text-decoration: none;
  color: $titleTextColor;

  &:first-child {
    border-top: 1px solid $borderColor;
  }

  &:hover {
    .nav__icon {
      color: $primaryColor;
      opacity: 1;
    }
    .nav__note {
      opacity: 0.6;
    }
  }

  &:active {
    .nav__icon {
      color: $primaryColor;
      opacity: 0.3;
    }
    .nav__note {
      opacity: 0.3;
    }
  }

  @media screen and (min-width: $tablet-width) {
    display: flex;
    border: none;
    padding: 36px 32px 34px;

    &:first-child {
      border: none;
    }
  }

  &--cart {
    @media screen and (min-width: $tablet-width) {
      padding-right: 0;
    }
  }
}

.nav__icon {
  position: absolute;
  left: 30px;
  opacity: 0.2;

  @media screen and (min-width: $tablet-width) {
    position: relative;
    left: 0;
    margin-right: 30px;
    display: block;
  }

  &--search {
    @media screen and (min-width: $tablet-width) {
      margin-right: 0;
    }
  }
}

.nav__note {
  @media screen and (min-width: $tablet-width) {
    display: block;
  }

  &--search {
    @media screen and (min-width: $tablet-width) {
      display: none;
    }
  }
}
